html {
  scroll-behavior: smooth;
}
.Navbar {
  background-color: #1c1b1b;
  position: fixed;
  top: 0px;
  display: flex;
  width: 100%;
  z-index: 999; /* Adjust the z-index as needed */
}
.Navbar a:nth-last-child(4){
  margin-left: auto; /* Push the last three headers to the right */
}

.Navbar a{
  color: rgb(198, 196, 196);
  font-weight: bold;
  font-size: 35px;
  text-decoration: none;
  padding: 10px 15px;


}
.Navbar a:first-child {
  color: rgb(198, 196, 196);
  font-weight: bold;
  font-size: 35px;
  text-decoration: none;
  padding: 10px 15px;

}


.Navbar a:hover{
  color:white;
  transform: scale(1.005);
  cursor: pointer;
}


.Home {
  display: flex;
  height: 100vh;
  flex-direction: column; /* Ensure flex items stack vertically */
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 40px;
  text-align: left;
}

.Home p {
  margin: 0; /* Reset margin for paragraphs */
}

.About {
  display: flex;
  flex-direction: column; /* Ensure flex items stack vertically */
  justify-content: center;
  align-items: center;
  height: 600px;
  color: #ffffff;
  font-size: 40px;
  flex-wrap: wrap; /* Allow flex items to wrap onto multiple lines */
}

.About p {
  margin: 0; /* adds 1 unit of spacing after each paragraph */
}

.Projects {
  display: flex;
  flex-direction: column; /* Ensure flex items stack vertically */
  justify-content: center;
  align-items: center;
  height: 600px;
  color: #ffffff;
  font-size: 40px;
  flex-wrap: wrap; /* Allow flex items to wrap onto multiple lines */
}
.Contact {
  display: flex;
  flex-direction: column; /* Ensure flex items stack vertically */
  justify-content: center;
  align-items: center;
  height: 600px;
  color: #ffffff;
  font-size: 40px;
  flex-wrap: wrap; /* Allow flex items to wrap onto multiple lines */
}




.AbProjectsut p {
  margin: 0; /* adds 1 unit of spacing after each paragraph */
}



.Buttons{
  position: absolute;
  left: 50%;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: space-around;
  -webkit-transform: translate(-50%, 0%);
  transform: translate(-50%, 125%);
  
}
.Buttons button {
  background-color: transparent;
  
  border: none;
}

.Buttons .button-img:hover{
  transform: translate(0%, -2%);
}


.button-img {
  width: 70px;
  height: 70px;
}

body{
  background-color: #1c1b1b
}

